const OneShotResult: React.FC<{
    children: React.ReactNode;
    id: string;
    demos: string;
    demos_label: string;
    video: string;
    hidden: boolean;
}> = ({ children, id, demos, demos_label, video, hidden }) => {
    const [isLoading, setIsLoading] = useState(true);

    // When the video has loaded, set the loading state to false
    const handleLoadedData = () => {
        setIsLoading(false);
    };

    return (
        <div
            id={id}
            className={`grasp-result -pt-2 flex flex-row flex-wrap justify-items-center items-center mt-6 ${hidden ? 'hidden' : ''}`}
        >
            <div className="sm:basis-3/3 relative">
                {/* Show loading spinner while the video is loading */}
                {isLoading && (
                    <div className="absolute inset-0 flex justify-center items-center">
                        <div className="w-10 h-10 border-4 border-t-4 border-gray-200 border-t-blue-500 rounded-full animate-spin"></div>
                    </div>
                )}

                <video
                    autoPlay
                    muted
                    playsInline
                    controls
                    loop
                    alt={String(children)}
                    className={`rounded-lg !my-0 !sm:my-0 w-full mx-auto transition-opacity duration-500 ${isLoading ? 'opacity-0' : 'opacity-100'}`}
                    onLoadedData={handleLoadedData}
                >
                    <source src={video} type="video/mp4" />
                    Your browser does not support the video tag.
                </video>
            </div>
        </div>
    );
};


