                    <p>
                        The keypoints were derived from both the bottleneck image and its segmentation mask. Ideally, these keypoints should 
                        be applied only to the task-relevant object, <u>ignoring distracting objects and partial occlusions</u>, while maintaining <u>temporal consistency</u>. 
                        Our method demonstrated the best performance in achieving these objectives, as shown in the videos above. <br /><br />

                        In our method's visualization, the orange represents keypoint matches from SIFT+LightGlue (for viewpoint robustness), the green indicates 
                        inliers from SuperPoint+LightGlue (for illumination robustness) on a virtual plane attached to the object, and the magenta applies the 
                        bottleneck mask directly to the current images. For more details, please refer to our paper.
                    </p>


